/**
 * WordPress dependencies.
 */
import { render, Fragment, useState, useEffect } from '@wordpress/element';
import domReady from '@wordpress/dom-ready';
import { Panel, PanelBody, Disabled, Notice, ColorPalette, Button } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { applyFilters, addFilter } from '@wordpress/hooks';
import { compose } from '@wordpress/compose';
import { withSelect, withDispatch, useSelect } from '@wordpress/data';
import { getFragment } from '@wordpress/url';
import { store as coreStore } from '@wordpress/core-data';
// import { useGlobalStylesReset } from '@wordpress/edit-site';
// import { useGlobalStylesReset } from '@wordpress/block-editor';

import apiFetch from '@wordpress/api-fetch';
/**
 * Exteranl dependencies.
 */
import { replace, isEmpty, has, last, merge } from 'lodash';

/**
 * Inner dependencies.
 */
import ActivePostTypes from './components/ActivePostTypes';
import SettingsToolbarArea from './components/SettingsToolbarArea';
import '../../extensions/store/settings';
import { getParsedVar } from './utils';
import {
	ColorControl,
	GradientColorControl,
} from '../../extensions/controls/color-options';

import styling from './inline-css';

addFilter( 'gutenify-admin-settings', 'gutenify/admin/settings/heading', ( content ) => (
	[ ...content, <Fragment key="gutenify-admin-settings-heading">
		<h1>{ __( 'Site Options' ) }</h1>
		<p className="description">{ __( 'If you need any help please contact us from our support page.' ) }</p>
	</Fragment> ]
) );

const copySiteOptions = ( obj ) => {
	function handler( event ) {
		event.clipboardData.setData( 'text/plain', JSON.stringify( obj ) );
		event.preventDefault();
		document.removeEventListener( 'copy', handler, true );
	}

	document.addEventListener( 'copy', handler, true );
	document.execCommand( 'copy' );
};

const App = ( props ) => {
	const [ appState, setAppState ] = useState( {
		hasSettingChange: false,
		isSettingsDisabled: true,
		settingError: '',
		settingSuccess: '',
	} );

	const [ coreStyles, setCoreStyles ] = useState( {} );
	const [ gutenifyStyles, setGutenifyStyles ] = useState( {} );

	const updateSettings = ( settings ) => {
		setAppState( ( prev ) => {
			return {
				...prev,
				hasSettingChange: true,
			};
		} );
		props.setSettings( settings );
	};

	const urlFragment = getFragment( window.location.href );
	const activePanel = replace( urlFragment, '#', '' );

	const { styleId, gStyle } = useSelect( ( select ) => {
		const coreSelect = select( coreStore );
		return {
			styleId: coreSelect.__experimentalGetCurrentGlobalStylesId(),
			gStyle: coreSelect.__experimentalGetCurrentThemeBaseGlobalStyles(),
		};
	} );

	const getColorCode = ( colorString ) => {
		let colorCode = colorString;
		if ( colorString.indexOf( 'var(' ) > -1 ) {
			const parsedVar = getParsedVar( colorString );
			if ( ! isEmpty( parsedVar ) ) {
				if ( parsedVar.includes( 'preset' ) && parsedVar.includes( 'color' ) && has( gStyle, 'settings.color.palette.theme' ) ) {
					const presetColor = gStyle.settings.color.palette.theme.filter( ( themeColor ) => themeColor.slug === last( parsedVar ) );
					if ( ! isEmpty( presetColor ) ) {
						colorCode = presetColor[ 0 ].color;
					}
				} else if ( parsedVar.includes( 'custom' ) && parsedVar.includes( 'color' ) && has( gStyle, `settings.custom.color.${ last( parsedVar ) }` ) ) {
					colorCode = gStyle.settings.custom.color[ last( parsedVar ) ];
				}
			}

			if ( colorCode.indexOf( 'var(' ) > -1 ) {
				colorCode = getColorCode( colorCode );
			}
		}

		return colorCode;
	};

	useEffect( ( ) => {
		// if ( 'undefined' === styleId && 'undefined' === gStyle ) {
		// 	return false;
		// }
		if ( styleId && gStyle ) {
			// console.log( styleId, gStyle );
			apiFetch( {
				path: `/wp/v2/global-styles/${ styleId }`,
				method: 'get',
			} )
				.then( ( res ) => {
					console.log( res );
					console.log( merge( gStyle.styles, res.styles ) );
					setCoreStyles( merge( gStyle.styles, res.styles ) );
					setAppState( ( prev ) => {
						return {
							...prev,
							isSettingsDisabled: false,
						};
					} );
				} );
		}
	}, [ styleId, gStyle ] );

	useEffect( ( ) => {
		apiFetch( {
			path: `/gutenify/v1/get_site_options`,
			method: 'get',
		} )
			.then( ( res ) => {
				if ( res.success && res.response ) {
					setGutenifyStyles( res.response.styles );
				}
			} );
	}, [ ] );

	const textColor = has( coreStyles, 'color.text' ) ? getColorCode( coreStyles.color.text ) : '';
	const backgroundColor = has( coreStyles, 'color.background' ) ? getColorCode( coreStyles.color.background ) : '';
	const h1Color = has( coreStyles, 'elements.h1.color.text' ) ? getColorCode( coreStyles.elements.h1.color.text ) : '';
	const buttonBackgroundColor = has( coreStyles, 'blocks.core/button.color.background' ) ? getColorCode( coreStyles.blocks[ 'core/button' ].color.background ) : '';
	const buttonTextColor = has( coreStyles, 'blocks.core/button.color.text' ) ? getColorCode( coreStyles.blocks[ 'core/button' ].color.text ) : '';

	const linkColor = has( coreStyles, 'elements.link.color.text' ) ? getColorCode( coreStyles.elements.link.color.text ) : '';
	const linkHoverColor = has( gutenifyStyles, 'elements.linkHover.color.text' ) ? getColorCode( gutenifyStyles.elements.linkHover.color.text ) : '';

	const buttonHoverTextColor = has( gutenifyStyles, 'blocks.core/button.hover.color.text' ) ? getColorCode( gutenifyStyles.blocks[ 'core/button' ].hover.color.text ) : '';
	const buttonHoverBackgroundColor = has( gutenifyStyles, 'blocks.core/button.hover.color.background' ) ? getColorCode( gutenifyStyles.blocks[ 'core/button' ].hover.color.background ) : '';
	// if ( has( coreStyles, 'color.text' ) ) {
	// 	if ( coreStyles.color.text.indexOf( 'var(' ) > -1 ) {
	// 		const parsedVar = getParsedVar( coreStyles.color.text );
	// 		if ( ! isEmpty( parsedVar ) && parsedVar.includes( 'preset' ) && parsedVar.includes( 'color' ) ) {
	// 			if ( has( gStyle, 'settings.color.palette.theme' ) ) {
	// 				const presetColor = gStyle.settings.color.palette.theme.filter( ( themeColor ) => themeColor.slug === last( parsedVar ) );
	// 				if ( ! isEmpty( presetColor ) ) {
	// 					textColor = presetColor[ 0 ].color;
	// 					setCoreStyles( {
	// 						...coreStyles,
	// 						color: {
	// 							...coreStyles.color,
	// 							text: presetColor[ 0 ].color,
	// 						},
	// 					} );
	// 				}
	// 			}
	// 		}
	// 	} else {
	// 		textColor = coreStyles.color.text;
	// 	}
	// }

	// console.log( useGlobalStylesReset, coreStyles );

	return <Disabled isDisabled={ appState.isSettingsDisabled }>
		{ applyFilters( 'gutenify-admin-settings', [], { ...props, setAppState, appState, updateSettings, activePanel } ) }
		<ColorControl
			label={ __( 'Text Color' ) }
			onChange={ ( color ) => {
				const data = merge( coreStyles, {
					color: {
						text: color,
					},
				} );
				setCoreStyles( { ...data } );
			} }
			value={ textColor }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Site Background Color' ) }
			onChange={ ( color ) => {
				const data = merge( coreStyles, {
					color: {
						background: color,
					},
				} );
				setCoreStyles( { ...data } );
			} }
			value={ backgroundColor }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Heading Color' ) }
			onChange={ ( color ) => {
				const data = merge( coreStyles, {
					elements: {
						h1: {
							color: {
								text: color,
							},
						},
						h2: {
							color: {
								text: color,
							},
						},
						h3: {
							color: {
								text: color,
							},
						},
						h4: {
							color: {
								text: color,
							},
						},
						h5: {
							color: {
								text: color,
							},
						},
						h6: {
							color: {
								text: color,
							},
						},
					},
				} );
				setCoreStyles( { ...data } );
			} }
			value={ h1Color }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Button background Color' ) }
			onChange={ ( color ) => {
				const data = merge( coreStyles, {
					blocks: {
						'core/button': {
							color: {
								background: color,
							},
						},
					},
				} );
				setCoreStyles( { ...data } );
			} }
			value={ buttonBackgroundColor }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Button Text Color' ) }
			onChange={ ( color ) => {
				const data = merge( coreStyles, {
					blocks: {
						'core/button': {
							color: {
								text: color,
							},
						},
					},
				} );
				setCoreStyles( { ...data } );
			} }
			value={ buttonTextColor }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Link Color' ) }
			onChange={ ( color ) => {
				const data = merge( coreStyles, {
					elements: {
						link: {
							color: {
								text: color,
							},
						},
					},
				} );
				setCoreStyles( { ...data } );
			} }
			value={ linkColor }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Link Hover Color' ) }
			onChange={ ( color ) => {
				const data = merge( gutenifyStyles, {
					elements: {
						linkHover: {
							color: {
								text: color,
							},
						},
					},
				} );
				setGutenifyStyles( { ...data } );
			} }
			value={ linkHoverColor }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Button Hover Text Color' ) }
			onChange={ ( color ) => {
				const data = merge( gutenifyStyles, {
					blocks: {
						'core/button': {
							hover: {
								color: {
									text: color,
								},
							},
						},
					},
				} );
				setGutenifyStyles( { ...data } );
			} }
			value={ buttonHoverTextColor }
			clearable={ false }
		/>
		<ColorControl
			label={ __( 'Button Hover Background Color' ) }
			onChange={ ( color ) => {
				const data = merge( gutenifyStyles, {
					blocks: {
						'core/button': {
							hover: {
								color: {
									background: color,
								},
							},
						},
					},
				} );
				setGutenifyStyles( { ...data } );
			} }
			value={ buttonHoverBackgroundColor }
			clearable={ false }
		/>
		<Button onClick={ () => {
			apiFetch( {
				path: `/wp/v2/global-styles/${ styleId }`,
				method: 'POST',
				data: {
					styles: coreStyles,
				},
			} )
				.then( ( res ) => {
					console.log( res );
				} )
				.catch( ( err ) => {
					console.log( err );
				} );

			apiFetch( {
				path: `/gutenify/v1/update_site_options`,
				method: 'POST',
				data: {
					styles: gutenifyStyles,
				},
			} )
				.then( ( res ) => {
					console.log( res );
				} )
				.catch( ( err ) => {
					console.log( err );
				} );

			const css = styling( coreStyles, gutenifyStyles );
			const adminCss = styling( coreStyles, gutenifyStyles, true );
			apiFetch( {
				path: `/gutenify/v1/update_global_styles`,
				method: 'POST',
				data: {
					css,
					adminCss,
				},
			} )
				.then( ( res ) => {

				} )
				.catch( ( err ) => {
					console.log( err );
				} );
		} } variant="primary">Save settings</Button>
		<Button onClick={ () => {
			if ( ! window.confirm( 'Are you sure to reset?' ) ) {
				return false;
			}
			apiFetch( {
				path: `/wp/v2/global-styles/${ styleId }`,
				method: 'POST',
				data: {
					styles: {},
				},
			} )
				.then( ( res ) => {
					console.log( gStyle.styles );
					setCoreStyles( {} );
					setCoreStyles( { ... gStyle.styles } );
				} )
				.catch( ( err ) => {
					console.log( err );
				} );

			apiFetch( {
				path: `/gutenify/v1/update_site_options`,
				method: 'POST',
				data: {
					styles: {},
				},
			} )
				.then( ( res ) => {
					console.log( res );
					setGutenifyStyles( {} );
				} )
				.catch( ( err ) => {
					console.log( err );
				} );

			apiFetch( {
				path: `/gutenify/v1/update_global_styles`,
				method: 'POST',
				data: {
					css: '',
					adminCss: '',
				},
			} )
				.then( ( res ) => {
					console.log( res );
				} )
				.catch( ( err ) => {
					console.log( err );
				} );

			location.reload();
		} } variant="secondary">Reset settings</Button>

		<Button onClick={ ( ) => {
			copySiteOptions( {
				styles: coreStyles,
			} );
		} }>Copy Core Options</Button>
		<Button onClick={ ( ) => {
			copySiteOptions( {
				styles: gutenifyStyles,
			} );
		} }>Copy Custom Options</Button>
	</Disabled>;
};

const AppCompose = compose( withSelect( ( select ) => {
	const { getSettings } = select( 'gutenify/settings' );
	return {
		settings: getSettings(),
	};
} ),
withDispatch( ( dispatch ) => {
	const { setSettings } = dispatch( 'gutenify/settings' );
	return {
		setSettings,
	};
} ) )( App );

domReady( function() {
	const appID = 'gutenify-site-options-app';
	if ( 'undefined' !== typeof document.getElementById( appID ) && null !== document.getElementById( appID ) ) {
		render( <AppCompose />, document.getElementById( appID ) );
	}
} );
